/* ==========================================================================
   Sidebar area styles - begin
   ========================================================================== */

aside.qodef-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .post-type-archive-product &{
        margin-top: 73px;
    }

    .widget {

        &:last-child {
            margin: 0 !important;

        }
	}
}

.wpb_widgetised_column {
    position: relative;

    .widget {
	
	    &:last-child {
		    margin: 0 !important;
	    }
    }
}
/* ==========================================================================
   Sidebar area styles - end
   ========================================================================== */

/* ==========================================================================
   Default WordPress Widgets styles - begin
   ========================================================================== */

.widget {
	margin: 0 0 30px;
	
	.qodef-widget-title {
		margin: 0 0 19px;
		
		a {
			color: inherit;
		}
	}
	
	ul {
		padding: 0;
		margin: 0;
		list-style: none;
		
		li {
			margin: 0 0 10px;
			
			&:last-child {
				margin: 0;
			}
		}
	}

    /*
    *   Specific styles
    */
    &.qodef-contact-form-7-widget{
        color: #fff;

        .input-button-wrapper{
            width: 100%;

            input{
                width: 100%;
            }
        }

        &.one-line-with-submit {

            .wpcf7-form-control-wrap{
                width: 100%;


                > input {
                    padding-right: 30%;
                }
            }

            .wpcf7-form-control.wpcf7-submit{
                display: inline-block;
                position: absolute;
                border: 0 !important;

                > span {
                    position: absolute;
                    line-height: 50px;
                    @include qodefTransform(translateX(-120%));
                }
            }
        }
    }

    &.widget_text {

        img {
            display: block;
        }
    }

    #wp-calendar {
        width: 100%;
        text-align: center;

        caption {
            color: $default-heading-color;
            margin: 0 0 15px;
        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            color: #fff;
            background-color: $first_main_color;

            a {
                color: inherit;
            }
        }
    }

    &.widget_rss {

        .qodef-widget-title {

            .rsswidget {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .rss-widget-icon {
            display: inline-block;
        }

        ul {

            li {
                margin: 0 0 25px;

                .rss-date {
                    display: block;
                }
            }
        }
    }

    &.widget_search {

        form {
            @include qodefRelativeHolderLayout();
            clear: both;

            > div {
                @include qodefRelativeHolderLayout();
                box-sizing: border-box;
                background-color: $additional-background-color;
            }
        }

        input,
        button {
            position: relative;
            display: inline-block;
            vertical-align: top;
            float: left;
            margin: 0;
            height: 60px;
            line-height: 32px;
            color: $default-heading-color;
            background-color: transparent;
            border: 0;
            border-radius: 0;
            outline: 0;
            -webkit-appearance: none;
            box-sizing: border-box;
            @include qodefTransition(color .2s ease-out);
        }

        input {
            width: calc(100% - 40px);
            padding: 10px 10px 10px 20px;
            font-family: $additional-text-font;
            font-size: 12px;
	
	        @include qodefPlaceholder {
		        color: inherit;
	        }
        }

        button {
            width: 40px;
            padding: 10px 0;
            font-size: 19px;
            color: $default-text-color;
            cursor: pointer;
            @include qodefTransition(color .2s ease-in-out);

            &:hover {
                color: $first-main-color;
            }

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }
    }

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_comments,
    &.widget_nav_menu {

        ul {

            li {
                margin: 0;
                padding: 4px 0;

                &:last-child {
                    border: none;
                }

                ul {
                    margin: 4px 0 0;
                    padding: 0 0 0 10px;

                    li {

                        &:last-child {
                            padding: 4px 0 0;
                        }
                    }
                }
            }
        }
    }

    &.widget_categories {

        ul {

            li {
                margin: 2px 0;
                padding: 0;

                a{
                    font-size: 12px;
                    text-transform: uppercase;
                    font-weight: 700;
                    letter-spacing: .1em;

                }

                ul {
                    margin: 0 0 0 10px;
                }
            }
        }
    }

    &.widget_recent_entries {
	
	    .qodef-widget-title {
            margin: 0 0 17px;
        }

        ul {

            li {
                margin: 0 0 8px;

                > a{
                    font-size: 15px;
                    font-weight: 700;
                    display: inline-block;
                    line-height: 21px;
                }

                > span {
                    display: block;
	                font-size: 12px;
                    font-family: $additional-text-font;
                    font-weight: 300;
                    line-height: 12px;
                    padding: 4px 0px;
                }
            }
        }
    }

    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        select {
            width: 100%;
            max-width: 100%;
            height: 54px;
            padding: 0 18px;
            line-height: 54px;
            color: $default-text-color;
            background-color: transparent;
            border: 1px solid $default-border-color;
            font-family: inherit;
            font-size: inherit;
            font-style: inherit;
            font-weight: inherit;
            outline: none;
            box-sizing: border-box;
        }
    }

    &.posts_holder {

        ul {
            
            li {

                &.page_item_has_children {

                    & > a {
                        display: block;
                    }

                    & > ul.children {
                        padding-left: 10px;
                    }
                }

                &.menu-item-has-children {

                    & > a {
                        display: block;
                    }

                    & > ul.sub-menu {
                        padding-left: 10px;
                    }
                }
            }
        }
    }

    &.widget_tag_cloud {

        .qodef-widget-title {
            margin: 0 0 28px;
        }

        a {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            padding: 0 17px;
            line-height: 40px;
            font-family: inherit;
            font-weight: 700;
            font-size: 10px !important;
            letter-spacing: 1px;
			text-transform: uppercase;
            background-color: #fafafa;
            margin-bottom: 3px;
            @include qodefTransition(all .2s ease-out);

            &:hover {
                color: #fff;
                background-color: $first-main-color;
            }
        }
    }
    
    &.widget_media_image {
        margin-bottom: 26px;
        
        a, img {
            display: block;
        }
    }
}

.vc_wp_search {

    .widget {

        &.widget_search {

            form {

                > div {
                    background-color: #fff;
                    border: none;
                }
            }

            input {
                width: calc(100% - 66px);
                padding: 0 0 0 33px;
                font-size: 15px;
            }

            button {
                width: 66px;
                padding: 0 39px 0 9px;
            }
        }
    }
}

.qodef-page-header,
.qodef-top-bar,
.qodef-sticky-header,
.qodef-mobile-header {
    
    .widget {
        margin: 0;
        
        &.widget_pages,
        &.widget_archive,
        &.widget_categories,
        &.widget_meta,
        &.widget_recent_entries,
        &.widget_recent_comments,
        &.widget_rss,
        &.widget_calendar,
        &.widget_rss,
        &.widget_archive,
        &.widget_categories,
        &.widget_tag_cloud,
        &.widget_nav_menu,
        &.widget_search {
            display: none !important;
        }
    }
}

.qodef-side-menu {

    .widget {
        color: $default-text-color;

        &:last-child {
            margin: 0 !important;
        }

        h1, h2, h3, h4, h5, h6 {
            margin: 0;
            color: $default-text-color;
        }

        .qodef-widget-title {
            color: $default-text-color;
            margin: 0 0 27px;
        }

        a {
            color: $default-text-color;

            &:hover {
                color: $first-main-color;
            }
        }

        table thead tr,
        table tbody tr {
            border-color: $default-text-color;
        }

        table tbody th {
            border-right-color: $default-text-color;
        }

        /*
        *   Specific styles
        */
        #wp-calendar {

            caption {
                color: $default-text-color;
            }
        }

        &.widget_rss {

            .qodef-footer-widget-title {

                .rsswidget {
                    color: $default-text-color;

                    &:hover {
                        color: $first-main-color;
                    }
                }
            }
        }

        &.widget_search {

            form {

                > div {
                    border-color: $default-text-color;
                }
            }

            input {
                color: $default-text-color;

                @include qodefPlaceholder {
                    color: inherit;
                }

                &[type='submit'] {
                    color: $default-text-color;
                }
            }

            button {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        &.widget_archive,
        &.widget_categories,
        &.widget_text {

            select {
                color: $default-text-color;
                border-color: $default-text-color;

                option {
                    background-color: #363636;
                }
            }

        }

        &.widget_tag_cloud {

            a {
                color: $default-text-color;

                &:after {
                    color: inherit;
                }

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .custom-html-widget {
            a {
                color: inherit;
                &:hover {
                    color: $first-main-color;
                }
            }
        }
    }
}

.qodef-page-footer {

    .qodef-btn.qodef-btn-simple, .qodef-btn.qodef-btn-simple:not(.qodef-btn-custom-hover-color):hover{
        color: #fff;

        &:hover{
            color: $first-main-color !important;
        }
    }

    .widget {
	    color: #fff;

        .widgettitle, .qodef-widget-title-holder{

            .qodef-widget-title{
                font-family: $default-text-font;
                font-size: 19px;
                font-weight: 700;
            }
        }

        &.qodef-contact-form-7-widget{
            input, textarea{
                color: #fff;
                font-size: 12px;
            }

            &.one-line-with-submit {
                .wpcf7-not-valid-tip {
                    display: none;
                }
            }
        }
	
	    &:last-child {
		    margin: 0 !important;
	    }
	    
        h1, h2, h3, h4, h5, h6 {
            margin: 0;
	        color: #fff;
        }
	
	    .qodef-widget-title {
		    color: #fff;
		    margin: 0 0 17px;
	    }
	
	    a {
		    color: #fff;
		
		    &:hover {
			    color: $first-main-color;
		    }
	    }
	    
        table thead tr,
        table tbody tr {
            border-color: #fff;
        }

        table tbody th {
            border-right-color: #fff;
        }

        /*
        *   Specific styles
        */
	    #wp-calendar {
		
		    caption {
			    color: #fff;
		    }
	    }
	
	    &.widget_rss {
		
		    .qodef-footer-widget-title {
			
			    .rsswidget {
				    color: #fff;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	    
        &.widget_search {

            form {

                > div {
                    border-color: #fff;
                }
            }

            input {
                color: #fff;
	            
	            @include qodefPlaceholder {
		            color: inherit;
	            }

                &[type='submit'] {
                    color: #fff;
                }
            }

            button {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        &.widget_archive,
        &.widget_categories,
        &.widget_text {

            select {
                color: #fff;
                border-color: #fff;

                option {
                    background-color: #363636;
                }
            }

        }

        &.widget_tag_cloud {

            a {
                color: $default-heading-color;

                &:after {
                    color: inherit;
                }
            }
        }
    }

    .qodef-footer-bottom-holder {
        font-family: $default-text-font;

        a {
            color: #fff;

            &:hover {
                color: $first-main-color;
            }
        }

        .widget {

            p {
                margin: 0;
            }

            .widgettitle, .qodef-widget-title-holder{
                font-weight: 300;
                display: inline-block;

                .qodef-widget-title{
                    margin: 0;
                    font-family: $default-text-font;
                    font-size: 14px;
                    font-weight: 400;
                }
            }

        }

    }

}

.qodef-top-bar {

	a {
		color: #fff;

		&:hover {
			color: $first-main-color;
		}
	}

    .widget {
        display: inline-block;
        vertical-align: middle;
        margin: 0;

        p {
            margin: 0;
        }

        .widgettitle, .qodef-widget-title{
            font-size: 12px;
            color: #fff;
            font-weight: 300;
            display: inline-block;
            margin: 0 0 0 35px;
        }

        &.widget_text {
            margin: 0 8px 0 0;
        }

        &.widget_nav_menu {

            ul {
                clear: both;

                li {
                    float: left;
                    padding: 0 8px;
                    margin: 0;
                    line-height: 14px;
                    border-left: 1px solid #fff;
                }
            }
        }
    }
}
/* ==========================================================================
   Default WordPress Widgets styles - end
   ========================================================================== */