/* ==========================================================================
   #Blocks styles - start
   ========================================================================== */

//! Paragraphs
p.has-background {
    padding: 20px 30px;
}

//! Audio
.wp-block-audio {
    width: 100%;

    audio {
        width: auto;
    }

    &.alignleft audio,
    &.alignright audio {
        max-width: 50%;
    }

    &.aligncenter {
        audio {
            display: block;
            margin: 0 auto;
        }
    }
}

//! Video
.wp-block-video {

    video {
        width: 100%;
    }
}

//! Button
.wp-block-button {

    .wp-block-button__link {
        @include qodefButtonDefaultStyle();
        @include qodefButtonSize();
        @include qodefButtonSolidColor();
        cursor: pointer;

        &:hover {
            @include qodefButtonSolidHoverColor();
        }
    }

    &.is-style-outline .wp-block-button__link {
        @include qodefButtonOutlineColor();
    }

    &.is-style-outline .wp-block-button__link:hover {
        @include qodefButtonOutlineHoverColor();
    }
}

//! Latest posts, categories, archives
.wp-block-archives,
.wp-block-categories,
.wp-block-latest-posts {
    margin: 0;
    padding-left: 0;

    ul {
        padding-left: 0 !important;
    }

    li {
        margin-bottom: 6px;
        list-style-type: none;

        a {
            color: $default-heading-color;
            text-decoration: none;

            &:hover {
                color: $first-main-color;
            }
        }
    }

    &.aligncenter {
        text-align: center;
    }
}

//! Latest categories
.wp-block-categories {

    li ul {
        margin: 4px 0 0;
        padding: 0 0 0 10px;
        list-style-type: none;

        li {

            &:last-child {
                padding: 4px 0 0;
            }
        }
    }
}

//! Latest posts grid view
.wp-block-latest-posts.is-grid {

    li {
        margin: 0 16px 16px 0;
    }
}

//! Latest preformatted text
.wp-block-preformatted {
    padding: 15px;
    white-space: pre-wrap;
    word-wrap: break-word;
}

//! Verse
.wp-block-verse {
    font-family: inherit;
    font-size: 22px;
    line-height: 1.8;
}

//! Paragraphs
.has-drop-cap {

    &:not(:focus):first-letter {
        font-family: inherit;
        font-size: 3.375em;
        line-height: 1;
        font-weight: 700;
        margin: 0 0.25em 0 0;
    }
}

//! Pullquote
blockquote.wp-block-pullquote {

    padding: 2.3% 6.8% !important;

    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;

    background: url("../../assets/img/quote-bg.png") no-repeat left 35px center !important;

    &.aligncenter {
        width: 100%;
    }

    &.alignleft {
        padding-left: 45px;
        text-align: left;
        max-width: 100%;

        background: url("../../assets/img/quote-bg.png") no-repeat right 35px center !important;

    }

    &.alignright {

        padding-right: 45px;
        text-align: right;
        max-width: 100%;

    }

    p {
        @include qodefTypographyLayout(!important);
        margin: 0;
    }
}

.wp-block-pullquote {

    margin: 0;

    blockquote {
        width: auto;
        text-align: center;
    }

    p {
        @include qodefTypographyLayout(!important);
    }
}

//! Blockquote
.wp-block-quote {

    padding: 2.3% 6.8% !important;

    > p {
        @include qodefTypographyLayout(!important);
        margin: 0;
    }

    > * {
        @include qodefTypographyLayout();
        margin: 0;
    }

    &.is-large,
    &.is-style-large {
        margin: 1rem 0;
        font-size: 1.6875em !important;
        line-height: 1.6em !important;


    }
}

//! Image
.wp-block-image {

    img {
        display: block;
    }

    &.alignleft,
    &.alignright {
        max-width: 100%;
    }

    &.alignfull img {
        width: 100vw;
    }
}

//! Cover Image
.wp-block-cover-image,
.wp-block-cover {
    position: relative;
    min-height: 430px;
    padding: 1rem;
    box-sizing: border-box;

    @media only screen and (min-width: 768px) {
        padding: 1rem 10%;
    }

    .wp-block-cover-image-text,
    .wp-block-cover-text,
    h2 {
        @include qodefH3();
        font-size: 28px !important;
        margin-bottom: 0;
    }

    &.aligncenter {

        h2,
        .wp-block-cover-image-text,
        .wp-block-cover-text {
            width: 100%;
            z-index: 1;
            left: 50%;
            position: absolute;
            transform: translate(-50%, -50%);
            top: 50%;
        }
    }

    &.alignleft,
    &.alignright {
        width: 100%;
    }

    &.alignfull {

        .wp-block-cover-image-text,
        .wp-block-cover-text,
        h2 {
            max-width: calc(8 * (100vw / 12) - 28px);
        }
    }
}

//! Galleries
.wp-block-gallery {
    list-style-type: none;
    padding-left: 0;

    .blocks-gallery-image:last-child,
    .blocks-gallery-item:last-child {
        margin-bottom: 16px;
    }
}

//! Captions
.wp-block-audio figcaption,
.wp-block-video figcaption,
.wp-block-image figcaption,
.wp-block-gallery .blocks-gallery-image figcaption,
.wp-block-gallery .blocks-gallery-item figcaption {
    font-size: 0.71111em;
    font-family: inherit;
    line-height: 1.6;
    margin: 0;
    padding: 0.5rem;
    text-align: center;
}

//! Separator
.wp-block-separator,
hr {
    background-color: #767676;
    border: 0;
    height: 2px;
    margin-bottom: 2rem;
    margin-top: 2rem;
    max-width: 100px;
    text-align: left;

    &.is-style-wide {
        max-width: 100%;
    }

    &.is-style-dots {
        max-width: 100%;
        background-color: inherit;
        border: inherit;
        height: inherit;
        text-align: center;

        &:before {
            color: #767676;
            font-size: 1.6875em;
            letter-spacing: 0.88889em;
            padding-left: 0.88889em;
        }
    }

    /* Remove duplicate rule-line when a separator
     * is followed by an H1, or H2 */
    & + h1,
    & + h2 {

        &:before {
            display: none;
        }
    }
}

//! Twitter Embed
.wp-block-embed-twitter {
    overflow: hidden;
}

//! Embed Video
.wp-block-embed {
    margin: 0;
}

//! Table
.wp-block-table {

    th,
    td {
        border-color: $default-border-color;
    }
}

//! File
.wp-block-file {
    font-family: inherit;

    .wp-block-file__button {
        @include qodefButtonDefaultStyle();
        @include qodefButtonSize(small);
        @include qodefButtonSolidColor();
        cursor: pointer;

        &:hover {
            @include qodefButtonSolidHoverColor();
        }
    }
}

//! Code
.wp-block-code {
    border-radius: 0;

    code {
        font-size: 1.125em;
        white-space: pre-wrap;
        word-break: break-word;
    }
}

//! Columns
.wp-block-columns {

    @media only screen and (min-width: 768px) {
        .wp-block-column > * {

            &:first-child {
                margin-top: 0;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }

        &[class*='has-'] > * {
            margin-right: 1rem;

            &:last-child {
                margin-right: 0;
            }
        }
    }
}

//! Latest Comments
.wp-block-latest-comments {

    .wp-block-latest-comments__comment-meta {
        font-weight: 700;

        .wp-block-latest-comments__comment-date {
            font-weight: normal;
        }
    }

    .wp-block-latest-comments__comment,
    .wp-block-latest-comments__comment-date,
    .wp-block-latest-comments__comment-excerpt p {
        font-size: inherit;
    }
}

//! Custom colors
.has-primary-background-color {
    background-color: $first-main-color;

    p,
    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    a {
        color: #fff;
    }
}

/* ==========================================================================
   #Blocks styles - end
   ========================================================================== */