@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.qodef-comment-holder {
    @include qodefRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .qodef-comment-holder-inner {
        padding: 33px 0 20px;
    }

    .qodef-comment {

        &.qodef-pingback-comment {

            .qodef-comment-text {
                padding: 0;
            }
        }
    }

    .qodef-comment-list {
        position: relative;
        padding: 0;
        margin: 0;
        list-style: none;
        z-index: 150;

        .qodef-comment{
            background-color: #fafafa;
            padding: 54px 34px;
            display: table;
            width: 100%;
            box-sizing: border-box;
        }


        li {
            @include qodefRelativeHolderLayout();
            margin: 25px 0 0;
            padding: 20px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 115px;
        }
    }

    .qodef-comments-title {
        margin-bottom: 37px;

        > * {
            margin: 0;
        }
    }

    .qodef-comment-image {
        width: 150px;
        display: table-cell;
        vertical-align: middle;

        img {
            display: block;
            border-radius: 50%;
            object-fit: contain;
            width: 100%;
        }
    }

    .qodef-comment-text {
        display: table-cell;
        vertical-align: middle;
        padding-left: 34px;
        width: calc(100% - 150px);
        box-sizing: border-box;

        .qodef-comment-info {
            @include qodefRelativeHolderLayout();
            margin: 0;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            margin-right: 10px;
            font-size: 12px;
            font-weight: 500;
            text-transform: uppercase;
            color: #000;
            letter-spacing: .1em;

            &:hover {
                color: $first-main-color;
            }

            &:before{
                content: "\76";
                font-family: 'ElegantIcons';
                text-transform: none;
                font-size: 16px;
                padding-right: 7px;
                vertical-align: bottom;
            }
        }

        .qodef-comment-name {
            float: left;
            margin: 0;
            font-size: 19px;
        }

        .qodef-comment-date {
            margin: 0;
            font-size: 12px;
            line-height: 18px;
            font-weight: 300;
            font-family: $additional-text-font;
            color: #a1a1a1;
        }

        .comment-respond {
            margin: 40px 0 45px;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.qodef-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;

    .qodef-comment-form-inner {
        padding: 66px 0 78px;
    }

    .comment-reply-title {
        margin: 0 0 37px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .qodef-comment-holder {

        .qodef-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .qodef-comment-holder {

        .qodef-comment-list {

            .children {
                padding: 0;
            }
        }

        .qodef-comment-text {

            .qodef-comment-date,
            .qodef-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {

    .qodef-comment-holder {

        .qodef-comment-image {
            float: none;
            display: block;
        }

        .qodef-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
            display: block;
            width: 100%;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.qodef-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

#respond {
    .comment-form-cookies-consent {
        margin: -5px 0 10px;

        #wp-comment-cookies-consent {
            position: relative;
            bottom: -1px;
        }
    }
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }

}

div.wpcf7-response-output {
    margin: 2em 0 1em;
}

//#submit_comment, // added our button classes in comments.php
//input.wpcf7-form-control.wpcf7-submit, // added our button classes in contact-form-7-confing.php
.post-password-form input[type='submit']{
    @include qodefButtonDefaultStyle();
    @include qodefButtonSize();
    @include qodefButtonSolidColor();

    cursor: pointer;

    &:hover {
        @include qodefButtonSolidHoverColor();
    }
}

.wpcf7-submit {

    &.qodef-btn.qodef-btn-outline:not(.qodef-btn-custom-border-hover) {

        &:hover {
            color: $default-heading-color !important;
            background-color: transparent !important;
            border-color: $default-border-color !important;
        }
    }
}

p.form-submit,
.wpcf7-form p {
    margin: 0;
}

#respond textarea,
.qodef-style-form textarea,
.wpcf7-form textarea {
    display: block;
}

.qodef-two-columns-form-without-space {
    .wpcf7-form .ajax-loader {
        display: none;
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.qodef-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.qodef-slider-is-moving {
        .owl-item {
            .qodef-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.qodef-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include qodefTransform(translateY($pos));
            }
        }
    }

    &.qodef-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            padding: 0;
            margin: 0;
            color: $default-heading-color;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;

            @include qodefTransform(translateY(-50%));
            @include qodefTransition(color .2s ease-out);

            &:hover {
                color: $first-main-color;
            }

            > span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-size: 46px;
                line-height: 1;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .owl-prev {
            left: 30px;
        }

        .owl-next {
            right: 30px;
        }
    }

    .owl-dots {
        text-align: center;
        margin: 16px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;
            padding: 0;
            margin: 0 6px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;

            span {
                display: inline-block;
                vertical-align: middle;
                width: 12px;
                height: 12px;
                border-radius: 3em;
                background-color: transparent;
                border: 2px solid rgba($default-heading-color, .5);
                box-sizing: border-box;

                @include qodefTransition(background-color .2s ease-out, border .2s ease-out);
            }

            &:hover,
            &.active {

                span {
                    background-color: $default-heading-color;
                    border-color: $default-heading-color;
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .qodef-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .qodef-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                @include qodefTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.qodef-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .qodef-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
    .qodef-wrapper {
        padding: 0 !important;
    }

    .qodef-content {
        height: 100vh;
    }

    &.admin-bar {
        .qodef-content {
            height: calc(100vh - 32px);
        }
    }

    .qodef-content-inner {
        @include qodefTableLayout();
    }

    .qodef-page-not-found {
        @include qodefTableCellLayout();

        padding: 0 20%;
        text-align: center;

        .qodef-404-title {
            margin: 0;
        }

        .qodef-404-subtitle {
            margin: 35px 0 0;
        }

        .qodef-404-text {
            margin: 10px 0 0;
        }

        .qodef-btn {
            margin: 30px 0 0;

            &.qodef-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }

    @include ipad-landscape {
        .qodef-content {
            height: 100%;
            min-height: 100vh;
        }

        &.admin-bar {
            .qodef-content {
                height: 100%;
                min-height: calc(100vh - 32px);
            }
        }
    }

    @include ipad-portrait {
        .qodef-page-not-found {
            padding: 70px 30px 20px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#qodef-back-to-top {
    position: fixed;
    width: 41px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;

        @include qodefTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;

        @include qodefTransition(opacity 0.3s ease);
    }

    > span {
        position: relative;
        display: block;
        width: 39px;
        height: 39px;
        line-height: 40px;
        font-size: 15px;
        text-align: center;
        color: #fff;
        background-color: $first-main-color;
        border: 1px solid $first-main-color;
        overflow: hidden;
        border-radius: 50%;
        @include qodefTransition(color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out);

        i {
            @include qodefTransition(.3s);
        }

        &:hover {

            i {
                @include qodefTransform(translateY(-3px));
            }
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.qodef-self-hosted-video-holder {
    .qodef-video-wrap {
        .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}

/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

/* ==========================================================================
   #Search tweak begin
   ========================================================================== */

// WEBKIT
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    display: none;
}

// IE
input[type="search"]::-ms-clear {
    display: none;
}

input[type=text]::-ms-clear {
    display: none;
}

/* ==========================================================================
   #Search tweak end
   ========================================================================== */

.tp-withaction {
    cursor: pointer;
}

.qodef-custom-scroll-down-button {
    
    img {
        @include qodefAnimation(2s move-down infinite);
    }
}

@keyframes move-down {
    0%,
    20%,
    100% {
    	@include qodefTransform(translateY(4px));
    }
    50% {
    	@include qodefTransform(translateY(-4px));
    }
}